.spinner-border {
  width: 5rem;
  height: 5rem;
  color: var(--primary-color);
}

.spinner {
  min-height: 100vh;
}
.navbar {
  box-shadow: var(--shadow);
  padding: var(--padding-0);
}

.navbar .header-cta {
  padding: 0.5rem 1rem;
  display: inline-block;
  background: none;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
}

.disabled-link {
  pointer-events: none;
}

.logo {
  width: 40%;
}
.bi-list {
  margin: 0 0.5em;
  cursor: pointer;
}

.feature-img {
  padding: 11rem;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: var(--light-blue);
  box-shadow: 0 15px 45px 0px var(--light-blue);
  border-radius: var(--radius);
}
.feature .feature-img {
  width: 400px;
}

.features-section .feature {
  margin: 2.5rem 0;
}
.feature-title {
  font-size: 1.75rem;
  font-weight: 600;
  margin-top: 1rem;
  letter-spacing: -0.9px;
}

.feature-1 {
  background-image: url(/static/media/TaskLife-EasyToUse.d60571bd.png);
  background-position: left;
}
.feature-2 {
  background-image: url(/static/media/TaskLife-QuickAnalytics.2521030a.png);
  background-position: right;
}

.hero-bg {
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  padding-top: 4em;
}

.hero-image .img-fluid {
  max-width: 75%;
  margin: 3rem auto;
  margin-bottom: 0;
}

.hero-image {
  margin-bottom: 0;
}

.hero-title {
  text-align: center;
  font-size: 3rem;
  line-height: 1.1;
  letter-spacing: -2.5px;
}

.hero-secondary {
  font-size: 1.3rem;
  text-align: center;
}

.hero-buttons {
  grid-gap: 15px;
  gap: 15px;
  margin: 0 auto;
}

.hero-title,
.hero-secondary {
  color: var(--black);
  width: 50%;
  margin: 2rem auto;
}

.hero-buttons .btn-login,
.hero-buttons .btn-demo-login {
  margin: 0.5rem auto;
  padding: 1rem 0.5rem;
  width: 20%;
}

.home-section {
  background-color: #eceeff;
}

.btn-demo-login {
  background-color: var(--black);
  color: var(--white);
}

.hero-buttons .btn-demo-login {
  background-color: var(--black);
  color: var(--white);
  font-weight: 600;
}

.hero-buttons .btn-demo-login:hover {
  background-color: var(--white);
  color: var(--black);
}

.features-section {
  margin: 5rem 0;
}

.footer {
  margin-top: 3rem;
}
.toast {
    padding: 1.2rem;
    border-radius: var(--radius);
    border-width: 0px;
}

.toast .toast-body {
    color: #fff;
    padding: 0;
}

.toast.notification-success {
    background-color: #31a503;
}

.toast.notification-danger {
    background-color: #dc3545;
}

.toast.notification-added {
    background-color: var(--primary-color);
}
.home-login-form {
  -webkit-transform: translateY(25%);
          transform: translateY(25%);
}

.form-logo {
  margin: 1.5rem auto;
  display: block;
}

.form-background.register-form-background {
  margin-top: 5em;
}
.home-register-form {
  -webkit-transform: translateY(5%);
          transform: translateY(5%);
}

.sidebar .offcanvas-title {
  color: var(--white);
}

.offcanvas.offcanvas-start {
  width: 300px !important;
}

.sidebar {
  margin-top: 0 !important;
}

.offcanvas-body,
.offcanvas-header {
  background-color: var(--primary-color);
}

.offcanvas .offcanvas-header {
  padding: 1rem 1.5rem;
}

.sidebar-link {
  font-size: 1rem;
  font-weight: 700;
  border-radius: var(--radius);
}

.sidebar-link,
.logout-menu {
  margin: 1rem;
}

.sidebar-link:hover,
.sidebar-link-active {
  background-color: var(--primary-hover);
}

.sidebar-link a {
  color: var(--white);
  display: inline-block;
  cursor: pointer;
  width: 100%;
}

.sidebar-link a,
.logout-menu {
  padding: 1rem 2rem;
}

.sidebar-link a:hover {
  color: var(--white);
}

.sidebar .offcanvas-body {
  padding: 1.5rem 0;
}

.logout-menu {
  cursor: pointer;
  fill: #fff;
}

.logout-menu p {
  font-weight: 600;
}

.logout-menu:hover {
  background-color: var(--primary-hover);
  border-radius: var(--radius);
}
.dashboard-header .title {
  font-weight: 600;
  font-size: 1rem;
}

.dashboard-header {
  padding: 0.8rem 2rem;
}

.dashboard {
  margin-left: 300px !important;
}

.dashboard-wrapper {
  background-color: var(--white);
  margin-top: 1.5rem;
  padding: 0.8rem 2rem;
  border-radius: 5px;
  min-height: 100vh;
}

.dashboard .container {
  margin: 0;
  max-width: 100%;
}

.dashboard-wrapper .dashboard-section {
  margin: 2rem 0;
}

.dashboard-heading,
.dashboard-sub-heading {
  font-weight: 600;
}

.dashboard-heading {
  font-size: 2rem;
}

.dashboard-sub-heading {
  font-size: 1.25rem;
}

.task-item {
  padding: 0.8rem;
  border: 2px solid var(--grey-2);
  border-radius: var(--radius);
}

.task-item:hover {
  box-shadow: var(--highlight-shadow);
  transition: all 0.2s;
  border: 2px solid var(--light-blue);
}

.task-label {
  font-size: 0.8rem;
  padding: 0 0.8rem;
  font-weight: 600;
  border-radius: var(--radius);
}

.due-date p {
  font-weight: 600;
}

.task-item .form-check-input[type="checkbox"] {
  background-color: var(--grey-2);
  border: none;
}

.task-item .done .form-check-input:checked[type="checkbox"] {
  background-color: var(--completed-bg);
}

.task-item .in-progress .form-check-input[type="checkbox"] {
  background-color: var(--pending-bg);
}

.task-data {
  width: 60%;
}

.task-status .form-check-input[type="checkbox"] {
  margin-top: 0;
}

.btn.btn--link {
  text-decoration: none;
}

.task-item-icons svg:hover,
.status-checkbox,
.form-check-input[type="checkbox"] {
  cursor: pointer;
}
.add-new-task-link {
  font-size: 1rem;
}
.task-item.add-new-task {
  border: 2px dashed var(--grey-2);
}

.stat-name {
  margin-bottom: 0;
}

.stat-name,
.stat {
  font-weight: 600;
}

.stat-card {
  text-transform: capitalize;
  border-radius: var(--radius);
}

.stat-container {
  margin: 0 0.2rem;
}

.dashboard-wrapper .stat-card {
  padding: 1.5rem;
}

.stat-link {
  color: inherit;
  font-size: 0.9rem;
  font-weight: 500;
}
.rounded-bar {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.inner-number {
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #ececec;
  color: var(--primary-text-color);
  font-size: 1.5rem;
}

.stat-container {
  box-shadow: var(--shadow);
  border-radius: var(--radius);
  background-color: var(--white);
}

.prod-percent {
  font-size: 2rem;
  font-weight: 600;
}

.prod-title {
  font-size: 1rem;
}

.prod-percent,
.prod-title {
  margin-bottom: 0;
}
:root {
  --primary-color: #3d56fb;
  --black: #232020;
  --white: #fff;
  --gradient: 50;
  --shadow: 0px 3px 10px 0px var(--light-blue);
  --form-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.15);
  --highlight-shadow: 0px 3px 10px 0px var(--light-blue);
  --radius: 12px;
  --grey: #ebebeb;
  --grey-2: #eeedeb;
  --primary-hover: #3145c9;
  --secondary-color: #ebebeb;
  --primary-text-color: #333333;
  --padding-0: 0.5rem;
  --padding-1: 1rem;
  --padding-2: 1.5rem;
  --padding-3: 3rem;
  --border-color: #ebebeb;
  --pending-bg: #f1c78e;
  --completed-bg: #9efdc7;
  --light-blue: #eceeff;
}

/*------------------------------
     FONT FAMILY     
 ------------------------------- */
@font-face {
  font-family: "inter";
  src: url(/static/media/inter-v12-latin-regular.1eb0efb3.woff);
}

@font-face {
  font-family: "inter";
  src: url(/static/media/inter-v12-latin-600.a53a38a4.woff);
  font-weight: 600;
}


/*------------------------------
     GLOBALS    
 ------------------------------- */

body {
  font-family: "inter";
  font-size: 1rem;
  color: #333333;
  color: var(--primary-text-color);
  letter-spacing: -0.2px;
  overflow-x: hidden;
}

body.modal-open {
  overflow-y: auto !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  letter-spacing: -0.7px;
}

a {
  text-decoration: none;
}

a {
  color: #3d56fb;
  color: var(--primary-color);
  font-weight: 600;
}

/*------------------------------
     BUTTONS
 ------------------------------- */
.btn--primary {
  background-color: #3d56fb;
  background-color: var(--primary-color);
  color: #fff;
  color: var(--white);
  font-size: 1rem;
  border: none;
  font-weight: 600;
}

.btn--primary:hover {
  background-color: #3145c9;
  background-color: var(--primary-hover);
  color: #fff;
  color: var(--white);
}

.btn--secondary {
  background-color: #ebebeb;
  background-color: var(--secondary-color);
  color: #232020;
  color: var(--black);
  font-size: 1rem;
  border: none;
  font-weight: 600;
}

.btn--secondary:hover {
  background-color: #fff;
  background-color: var(--white);
  color: #232020;
  color: var(--black);
}

.btn--delete {
  border: none;
}

/*------------------------------
     FORM STYLES
 ------------------------------- */
.form-background {
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.15);
  box-shadow: var(--form-shadow);
  padding: 2rem;
  border-radius: 12px;
  border-radius: var(--radius);
  background-color: #fff;
  background-color: var(--white);
}

.form-background h4 {
  letter-spacing: -0.9px;
}

.login-form,
.custom-form,
.register-form {
  margin: 0 auto;
}

.login-form input:focus,
.custom-form input:focus,
.register-form input:focus {
  box-shadow: 0 0 0 0.25rem rgb(41 64 211 / 25%);
}

/*------------------------------
    ICON STYLES
 ------------------------------- */
.logout-icon,
.pie-icon,
.list-icon {
  margin-right: 0.8rem;
}

.close-icon:hover {
  cursor: pointer;
}

/*------------------------------
     GLOBAL CLASSES
 ------------------------------- */
.border-bottom {
  border-bottom: 1px solid #ebebeb;
  border-bottom: 1px solid var(--border-color);
}

.delete-icon {
  cursor: pointer;
}

.sidebar {
  margin-top: 66px;
  background-color: #232020;
  background-color: var(--black);
}

/* Task status Styles */
.completed {
  background-color: #9efdc7;
  color: #0c321d;
}

.pending {
  background-color: #f1c78e;
  color: #4c3105;
}

.total {
  background-color: #ececec;
  color: #777777;
}

/* Navabr and Dashboard */
.modal-open {
  padding-right: 0 !important;
}

/* Logout Button */
.logout-btn {
  margin: 1rem 0;
}
/* Below 1000px */
@media (max-width: 62.5em) {

  .hero-buttons .btn-login,
  .hero-buttons .btn-demo-login {
    width: 25%;
  }

  .hero-title,
  .hero-secondary {
    width: 50%;
  }
}

/* Below 500px */
@media (max-width: 43.75em) {

  .hero-title,
  .hero-secondary {
    width: 90%;
  }

  .hero-title {
    font-size: 2.25rem;
    letter-spacing: -2px;
  }

  .hero-secondary {
    font-size: 1.1rem;
  }

  .hero-buttons .btn-login,
  .hero-buttons .btn-demo-login {
    width: 50%;
  }

  .form-background {
    width: 90%;
    margin: 0 auto;
  }

  .logo {
    width: 40%;
  }

  .navbar {
    padding: 0.7rem;
  }

  .dashboard-wrapper {
    padding: 1rem;
  }

  .modal-open .dashboard-wrapper {
    padding: 0 0.5rem;
  }

  .dashboard-header {
    padding: 0.8rem 1rem;
  }

  .features-section {
    margin-top: 4rem;
  }

  .feature {
    text-align: center;
  }

  .feature:nth-child(even) {
    flex-direction: column-reverse;
  }

  .task-label {
    display: none;
  }

  .hero-image .img-fluid {
    max-width: 100%;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1140px;
  }
}

@media (max-width: 768px) {
  .dashboard {
    margin-left: 0 !important;
  }
}

@media (max-width: 770px) {
  .stat-container {
    flex-direction: column;
  }
}

@media (min-width: 768px) {
  .stats-container .col-lg-3 {
    width: 32%;
  }
}
